<template>
  <section>

    <div class="showed-card">
      
      <wallet-header 
      headerTitle="ADD NEW CARD"
      headerText="NEW CARD"
      />

      

        <base-card
        
          :chipSource= chipSource
          :logoSource= logoSource
          :style="cardColor"
          :cardNumber= spaceEveryFour(inputNumber)
          :fullname= inputName
          :MM= inputMonth
          :YY= inputYear
        >
        </base-card>
      

    </div>


    <div class="form">
      <div class="form-number">
        <label for="inputNumber">CARD NUMBER</label>
        <input maxlength="16" v-model="inputNumber" id="inputNumber" type="text" placeholder="XXXX XXXX XXXX XXXX">
      </div>
      <div class="form-name">
        <label for="inputName">CARDHOLDER NAME</label>
        <input maxlength="18" v-model="inputName" id="inputName" type="text" placeholder="Firstname Lastname">
      </div>

      <div class="form-month-year">
        <div class="month">
          <label for="month">MONTH</label>
          <select @input="showSelMonth" ref="selectedMonth" id="month" name="month">
              <option value="123" selected="selected" style="display: none"></option>
              <option value="01">01</option>
              <option value="02">02</option>
              <option value="03">03</option>
              <option value="04">04</option>
              <option value="05">05</option>
              <option value="06">06</option>
              <option value="07">07</option>
              <option value="08">08</option>
              <option value="09">09</option>
              <option value="10">10</option>
              <option value="11">11</option>
              <option value="12">12</option>
          </select>
        </div>
        <div class="year">
          <label for="year">YEAR</label>
          <select @input="showSelYear" ref="selectedYear" name="year" id="year">
            <option value="999" selected="selected" style="display: none"></option>
            <option value="21">21</option>
            <option value="22">22</option>
            <option value="23">23</option>
            <option value="24">24</option>
            <option value="25">25</option>
          </select>
        </div>
      </div>

      <div class="form-vendor">
        <label for="vendor">VENDOR</label>
        <select @input="showVendor" ref="selectedVendor" name="vendor" id="vendor">
          <option style="display:none" selected="selected" value=""></option>
          <option value="bitcoin">Bitcoin Inc</option>
          <option value="blockchain">Blockchainn Inc</option>
          <option value="evil">Evil Corp</option>
          <option value="ninja">Ninja Bank</option>
        </select>
      </div>

    </div>
    <div class="btn-wrapper">
      <div @click="addCardToCardObject" class="btn">
        <router-link to="/home"><base-button :btn="btnT"></base-button></router-link>
      </div>
      <div class="btn">
        <router-link to="/home"><base-button btn="BACK TO CARDS"></base-button></router-link>
      </div>

    </div>


  </section>
</template>

<script>

import BaseButton from '@/components/Basebutton.vue';
import BaseCard from '@/components/BaseCard.vue';
import WalletHeader from '@/components/WalletHeader.vue';
export default {
  components:{
      BaseButton,
      BaseCard,
      WalletHeader
  },

  data(){
    return{
      btnT:'ADD CARD',
      
      inputNumber: '',
      inputName:'',
      inputMonth:'MM',
      inputYear:'YY',
      cardColor: 'backgroundColor: lightgrey; position:relative; margin-top: -160px',
      chipSource: '/img/chip-light.5bf3177c.svg',
      logoSource: ' '
    }
  },
 
  methods:{
    showSelMonth(){
      this.inputMonth= this.$refs.selectedMonth.value
      // console.log(this.$refs.selectedMonth)
    },
    showSelYear(){
      this.inputYear= this.$refs.selectedYear.value
    },
    showVendor(){
      if (this.$refs.selectedVendor.value == 'ninja') {
        this.cardColor = `background: rgb(37,37,37);background: linear-gradient(90deg, rgba(37,37,37,1) 8%, rgba(54,54,54,1) 75%, rgba(70,70,70,1) 100%); color: white; marginTop: -160px; position: relative`
        this.chipSource= '/wallet-app/img/chip-light.5bf3177c.svg'
        this.logoSource= '/wallet-app/img/vendor-ninja.046254ea.svg'
      }
      if (this.$refs.selectedVendor.value == 'bitcoin') {
        this.cardColor = `backgroundColor: #f5aa48; color:black; ; marginTop: -160px; position: relative`
        this.chipSource= '/wallet-app/img/chip-dark.fc147077.svg'
        this.logoSource= '/wallet-app/img/vendor-bitcoin.6d450848.svg'
      }
      if (this.$refs.selectedVendor.value == 'blockchain') {
        this.cardColor = `backgroundColor: #834ad7; color:white; ; marginTop: -160px; position: relative `
        this.chipSource= '/wallet-app/img/chip-light.5bf3177c.svg'
        this.logoSource= '/wallet-app/img/vendor-blockchain.a2171465.svg'
      }
      if (this.$refs.selectedVendor.value == 'evil') {
        this.cardColor = `background: linear-gradient(248.3deg, rgba(0, 0, 0, 0.16) 0%, rgba(0, 0, 0, 0) 100%), #F33355; color:white; ; marginTop: -160px; position: relative`
        this.chipSource= '/wallet-app/img/chip-light.5bf3177c.svg'
        this.logoSource= '/wallet-app/img/vendor-evil.1c4415c7.svg'
      }

    },

    spaceEveryFour(e){
      let finalString= ""
      for (let i = 0; i < e.length; i++) {
        (i % 4 == 3) ? finalString = finalString + this.inputNumber[i] + " " : finalString = finalString + this.inputNumber[i]
      }
      return finalString
    },
    // convertEveryFour(){
    //   console.log(this.inputNumber)
    //   this.inputNumber = this.spaceEveryFour(this.inputNumber.trim())
    //   console.log(this.inputNumber)
    // },

    addCardToCardObject(){

      // this.inputNumber = this.spaceEveryFourDigits(this.inputNumber)

      this.$root.pushToData(
        {
        id: this.$root.data.length + 50,
        inputNumber : this.spaceEveryFour(this.inputNumber),
        inputName : this.inputName,
        inputMonth : this.inputMonth,
        inputYear : this.inputYear,
        cardColor : this.cardColor,
        chipSource : this.chipSource,
        logoSource : this.logoSource}

      )
      
    }
  }
}
</script>

