<template>
  <div class="home">
     
    <div class="header-comp">
      <wallet-header
        :headerTitle= headerTittle
        :headerText= headerSubText
      />
    </div>

    <div v-if="showActiveCard"  class="base-card-comp">
      <base-card
        class="inactive"
        :chipSource= this.$root.activeCardData.chipSource 
        :logoSource= this.$root.activeCardData.logoSource
        :style= this.$root.activeCardData.cardColor
        :cardNumber= this.$root.activeCardData.inputNumber
        :fullname= this.$root.activeCardData.inputName
        
        :MM= this.$root.activeCardData.inputMonth
        :YY= this.$root.activeCardData.inputYear
      />
    </div>

    <div class="cards-pile-comp">
      <cards-pile
      
      > 
      </cards-pile>
    </div>

    <div class="btn">
      <div v-if="!this.$root.deleteBtnActive" class="add-card-btn">
        <router-link to="/new-card"><base-button :btn="btnT">  </base-button></router-link>

      </div>
      <div v-if="!emptyArray" @click="toggleDeleteStatus" class="delete-wrapper">
        <base-button :style="this.$root.deleteBtnActive? 'backgroundColor: black; color: white': ''" :btn="deleteMsgCC">  </base-button>
      </div>

    </div>

  </div>
</template>

<script>
import BaseButton from '@/components/Basebutton.vue';
import WalletHeader from '@/components/WalletHeader.vue';
import BaseCard from '@/components/BaseCard.vue';
import CardsPile from '@/components/CardsPile.vue';

export default {
  components:{
    BaseButton,
    WalletHeader,
    BaseCard,
    CardsPile
  },
  computed:{
    deleteMsgCC(){
      if (!this.$root.deleteBtnActive) {
        return 'Delete a card'
      }else{
        return 'Exit delete mode'
      }
    },
    emptyArray(){
      if (this.$root.data.length == 0) {
        return true
      }else{
        return false
      }
    },
    headerSubText(){
      if (this.emptyArray) {
        return "Your don't have any saved cards. Start by adding one"
      }
      if (this.$root.deleteBtnActive) {
        return ""
      }
      else{
        return 'ACTIVE CARD'
      }
    },
    headerTittle(){
      if (this.$root.cardIsChosen) {
        return " "
      }
      if(this.$root.deleteBtnActive){
        return "CLICK ON A CARD"
      }
      else{
        return "E-WALLET"
      }
    },
    showActiveCard(){
      if(this.$root.deleteBtnActive || this.emptyArray){
        return false
      }
      else{
        return true
      }
    }
  },
  

  data(){
    return{
      btnT:'ADD NEW CARD',
    }
  },
  methods:{
    toggleDeleteStatus(){
      this.$root.deleteBtnActive = !this.$root.deleteBtnActive
      this.$root.cardIsChosen = false
      // console.log(this.$root.data.length)
    }
  }

}
</script>
<style scoped>

.home{
  /* text-align: center; */
  padding: 2rem 0;
}
.cards-pile-comp{
  margin: 4rem 0;
}

.base-card-comp{
  margin-bottom: 200px;
  
}

.btn{
  text-align: center;
}

button{
  width: 100%;
}

.delete-wrapper button:visited{
  background-color: green;
  color: white;
}

.inactive{
  /* animation: moveUp 0.5s ease-out; */
  box-shadow: none; 
  animation: none;
}

.inactive:hover{
  cursor: context-menu;
  transform: none;
  transition-duration: .10s;
}

</style>

