<template>
  <div class="cards-pile-wrapper">
    <div v-if="!cardIsChosen" class="cards-pile-cards">

      <base-card
      v-for="(card, index) in usersData"
      :key="card.id"
    
      
      :style="card.cardColor"

      :cardIndex= index
      :chipSource= card.chipSource
      :logoSource= card.logoSource
      :cardNumber= card.inputNumber
      :fullname= card.inputName
      :MM= card.inputMonth
      :YY= card.inputYear
      :ref="card.id"
      >
      </base-card>
    </div>
     <!-- <span>{{index}}</span> -->
    <div v-if="this.$root.cardIsChosen" class="message">
      <p>Do you want to delete this card??</p>
      <div class="mini-card-wrapper">
        <base-card
        class="mini-card"
        :cardNumber= cardArray[index].inputNumber
        :fullname= cardArray[index].inputName
        :chipSource= cardArray[index].chipSource
        :logoSource= cardArray[index].logoSource
        :MM= cardArray[index].inputMonth
        :YY= cardArray[index].inputYear
        :style = cardArray[index].cardColor
        style="transform:scale(0.6)translateY(160px); letter-spacing: 0px !important;"
        >
      </base-card>

      </div>
      <div class="message-btn-container">
        <button @click="confimAndDelete">Yes</button>
        <button @click="abortDeleteMode">No</button>
      </div>
    </div>
  </div>
</template>

<script>
import BaseCard from "@/components/BaseCard.vue";
export default {
  components:{
    BaseCard
  },
  data(){
    return{
        usersData: this.$root.data,
        styleDeleteMode: 'box-shadow: 0px 0px 20px red',
        active: '.pile-base-card',
        inactive: '.inactive',
        cardArray : this.$root.data,
        // index: this.$root.indexToDelete
    }
  },
  computed:{
    index(){
      return this.$root.indexToDelete
    },
    cardIsChosen(){
      return this.$root.cardIsChosen
    }
  },

  methods:{
    abortDeleteMode(){
      this.$root.cardIsChosen = false
    },
    confimAndDelete(){
      this.$root.deleteCard()
      if (this.$root.data.length == 0) {
        
        this.$root.deleteBtnActive = false
      }
    }
  }
}
</script>

<style scoped>
.base-card:hover{
  cursor: pointer;
  transform: scale(1.05) translateY(-5px);
  transition-duration: .10s;
}


.cards-pile-wrapper{
  width: 300px;

  display: block;
  margin: 3rem 0;
  width: fit-content;
}

/* .cards-pile-cards{
  margin-bottom: 160px;
} */

.message{
  /* margin-top: -160px; */
  /* border: 2px solid black; */
  background-color: rgba(233, 233, 233, 0.603);
  border-radius: 10px;
  font-size: 1rem;
  color: black;
  font-weight: bold;
  width: 380px;
  
  padding: 0.5rem;
  text-align: center;
  margin-top: -160px ;
  text-transform: uppercase;

  box-shadow: 2px 2px 6px rgb(112, 112, 112);

  /* transform:translateY(-160px); */
}


.message button{
  font-size: 1rem;
  padding: 0.4rem 2rem;
  
  
  background-color: black;
  border: none;
  outline: none;
  color: white;
  border-radius: 8px;
}

.message-btn-container{
  display: flex;
  justify-content: space-around;
  margin-top: 2rem;
}

.mini-card-wrapper{
  display: flex;
  justify-content: center;
  align-items: center;
  height: 170px;
}

.mini-card{
  transform: scale(0.6) translateY(0px);
  box-shadow: none;
  margin-top: 0px;
  
}

/* .mini-card p{
  font-size: 1.8rem;
} */

.mini-card h6{
  text-align: left;
}

.mini-card:hover{
  cursor: context-menu;
  transform: scale(0.6) translateY(0px);
  transition-duration: .10s;
  
}

@keyframes moveUp{
        0%{
            opacity: 0;
            transform: translateY(40px);
        }
        50%{
            opacity: 0;
            transform: translateY(40px);
        }
        100%{
            transform: translateY(0);
        }
    }
  

@media only screen and (max-width: 600px) {
  .message{
    width: 330px;
  }
}

  
</style>
        
      