<template>

        <div ref="cardStyle" @click="callRootMethods()" :class="deleteBtnStatus? active: inactive">
            <div class="chip-logo">

            <img src="@/assets/chip-dark.svg" alt="chip-light no pic">
            <img :src="logoSource" alt="">
            </div>

            <div class="number-name-date">
                <div class="number">
                    <p>{{cardNumber}}</p>
                </div>

                <div class="name-date" >
                    <div class="name">
                        <h6>CARDHOLDER  NAME</h6>
                        <p>{{fullname.toUpperCase()}}</p>
                    </div>
                    <div class="date">
                        <h6>VALID UNTIL</h6>
                        <p>{{MM}} / {{YY}}</p>
                    </div>
                </div>
            </div>
        </div>

</template>

<script>
export default {

props:['chipSource', 'logoSource', 'cardNumber', 'fullname', 'MM', 'YY'],

methods:{
    callRootMethods(){
        if (this.$root.deleteBtnActive == false) {
            this.$root.updateActiveCard({
                inputNumber: this.cardNumber,
                inputName: this.fullname.toUpperCase(),
                inputMonth: this.MM,
                inputYear: this.YY,
                cardColor: this.$refs.cardStyle.style.cssText,
                chipSource: this.chipSource,
                logoSource: this.logoSource}
                )
        }else{

            this.$root.indexToDelete = this.$refs.cardStyle.attributes.cardindex.value
            this.$root.cardIsChosen = true
        }
            
    }
},
  computed:{
    deleteBtnStatus(){
      return this.$root.deleteBtnActive
    }
  },
data(){
    return{
        active: 'active',
        inactive: 'base-card'
    }
}
}
</script>

